/*视图类型*/
function isInvisualInteractiveCom(controlTypeID) {
  var isInvisualCom = false;
  var typeID = controlTypeID;
  if (typeID === 'PopupPanel' || typeID === 'HoverPanel' || typeID === 'AlertWindow' || typeID === 'PopupMenu' || typeID === 'SlidingDrawer') {
    isInvisualCom = true;
  }
  return isInvisualCom;
}

