/*链接坐标等遍历判断*/
function updateLinkCoords(comp) {
  var rect;

  if (LinkFitMode[comp.controlTypeID] === LinkAreaLayout.HORIZONTAL) { // 链接宽度始终与组件的宽度相等
    if(comp.links && comp.links.link) {
      _.each(comp.links.link, function (link) {
        rect = util.Rect.strToRect(link.area.coords);
        util.Rect.setSize(rect, comp.currentW || comp.w);
        link.area.coords = rect;
      });
    }
    if (comp.interactiveData && comp.interactiveData.entry) {
      _.each(comp.interactiveData.entry, function (entry) {
        rect = util.Rect.strToRect(entry.area.coords);
        util.Rect.setSize(rect, comp.currentW || comp.w);
        entry.area.coords = rect;
      });
    }
  } else if (LinkFitMode[comp.controlTypeID] === LinkAreaLayout.VERTICAL) {//链接高度始终与组件高度相等
    if(comp.links && comp.links.link) {
      _.each(comp.links.link, function (link) {
        rect = util.Rect.strToRect(link.area.coords);
        util.Rect.setSize(rect, rect.width, comp.currentH || comp.h);
        link.area.coords = rect;
      });
    }
    if (comp.interactiveData) {
      _.each(comp.interactiveData.entry, function (entry) {
        rect = util.Rect.strToRect(entry.area.coords);
        util.Rect.setSize(rect, rect.width, comp.currentH || comp.h);
        entry.area.coords = rect;
      });
    }
  } else if (LinkFitMode[comp.controlTypeID] === LinkAreaLayout.CLIENT) {//链接与组件尺寸相同
    if(comp.links && comp.links.link) {
      _.each(comp.links.link, function (link) {
        rect = util.Rect.strToRect(link.area.coords);
        util.Rect.setSize(rect, comp.currentW || comp.w, comp.currentH || comp.h);
        link.area.coords = rect;
      });
    }
    if (comp.interactiveData) {
      _.each(comp.interactiveData.entry, function (entry) {
        rect = util.Rect.strToRect(entry.area.coords);
        util.Rect.setSize(rect, comp.currentW || comp.w, comp.currentH || comp.h);
        entry.area.coords = rect;
      })
    }
  } else if (LinkFitMode[comp.controlTypeID] === LinkAreaLayout.H_AVG) {//链接水平等分组件
    if(comp.links && comp.links.link) {
      var left = 0;
      var count = comp.links.link.length || 1;
      var itemW = (comp.currentW || comp.w) / count;
      _.each(comp.links.link, function (link) {
        rect = util.Rect.strToRect(link.area.coords);
        util.Rect.setLocation(rect, left);
        util.Rect.setSize(rect, itemW, comp.currentH || comp.h);
        link.area.coords = rect;
        left += itemW;

      });
    }
    if (comp.interactiveData) {
      left = 0;
      _.each(comp.interactiveData.entry, function (entry) {
        rect = util.Rect.strToRect(entry.area.coords);
        util.Rect.setLocation(rect, left);
        util.Rect.setSize(rect, itemW, comp.currentH || comp.h);
        entry.area.coords = rect;
        left += itemW;
      })
    }
  } else if (LinkFitMode[comp.controlTypeID] === LinkAreaLayout.CUSTOM) {
    var link;
    var entry;
    if(comp.links && comp.links.link) {
      link = comp.links.link[0];
      rect = util.Rect.strToRect(link.area.coords);
      util.Rect.setLocation(rect, rect.left, ((comp.currentH || comp.h) - rect.height) / 2);
      link.area.coords = rect;

      link = comp.links.link[1];
      rect = util.Rect.strToRect(link.area.coords);
      util.Rect.setLocation(rect, (comp.currentW || comp.w) - 15 - rect.width, ((comp.currentH || comp.h) - rect.height) / 2);
      link.area.coords = rect;
    }
    if (comp.interactiveData) {

      entry = comp.interactiveData.entry[0];
      rect = util.Rect.strToRect(entry.area.coords);
      util.Rect.setLocation(rect, rect.left, ((comp.currentH || comp.h) - rect.height) / 2);
      entry.area.coords = rect;

      entry = comp.interactiveData.entry[1];
      rect = util.Rect.strToRect(entry.area.coords);
      util.Rect.setLocation(rect, (comp.currentW || comp.w) - 15 - rect.width, ((comp.currentH || comp.h) - rect.height) / 2);
      entry.area.coords = rect;
    }
  } else {
    if(comp.links && comp.links.link) {
      _.each(comp.links.link, function (link) {
        rect = util.Rect.strToRect(link.area.coords);
        link.area.coords = rect;
      });
    }
    if (comp.interactiveData) {
      _.each(comp.interactiveData.entry, function (entry) {
        rect = util.Rect.strToRect(entry.area.coords);
        entry.area.coords = rect;
      })
    }
  }
}

function isCompositeCom(typeID){
  return typeID === 'IconButton2' || typeID === 'IconLabel2' || typeID === 'AlarmIcon2';
}

function isIgnoreCom(typeID){
  return typeID ==='Border' || typeID === 'ScrollViewPort' || typeID === 'ScrollViewContentPanel';
}

function replaceComTypeID(comp){
  if(comp.controlTypeID === 'Circle'){
    comp.controlTypeID = 'Shape';
  }
}

function makeComp(comp, comEvents) {
  try {
    var c = comp;
    var Comp;

    updateLinkCoords(c);

    replaceComTypeID(c);

    if (isIgnoreCom(c.controlTypeID)) {
      return undefined;
    }
    Comp = Components[c.controlTypeID];

    if (Comp) {
      Comp.key = c.controlID; // react
      c.events = comEvents;
      if (c.controlTypeID === 'CompositeComboBox' && c.hasOwnProperty('baseHeight')) {
        c.h = c.baseHeight;
      } else if (isCompositeCom(c.controlTypeID)){
        c.textColor = c.items[2].textColor;
      }else if(c.controlTypeID === 'Timer'){
        c.interval = c.interval || 1000;
      }
      //包含有子组件的情况
      if (c.items) {
        //所有非可视化组件
        if (isInvisualInteractiveCom(c.controlTypeID)) {
          //其它弹出组件，它们的Content是一个组类型的组件
          //这里直接忽略掉背景这一层组件
          if (c.items[1].items) {
            return (
              <Comp {...c}>
                {
                  c.items[1].items.map(function (_comp) {
                    return makeComp(_comp, comEvents)
                  })
                }
              </Comp>
            );
          } else {
            return (<Comp {...c}>
              {
                makeComp(c.items[1], comEvents)
              }
            </Comp>);
          }
        } else if (c.controlTypeID === 'ContentPanel') {
          return (<Comp key={c.controlID} {...c}>
            {
              c.items.map(function (_comp, index) {
                _comp.h = c.h;
                _comp.w = c.w;
                _comp.y = 0;
                _comp.x = 0;
                _comp.contentLayout = c.contentLayout;
                _comp.canHoriScroll = c.canHoriScroll;
                _comp.canVertScroll = c.canVertScroll;
                _comp.showScrollBars = c.showScrollBars;
                return (
                  <LazyLoad disabled={c.selectedIndex === index} key={index}>
                    <div key={index} ref={index} style={{
                      width: c.w,
                      height: c.h,
                      left: 0,
                      top: 0,
                      position: 'absolute'
                    }}>
                      {makeComp(_comp, comEvents)}
                    </div>
                  </LazyLoad>);
              })
            }
          </Comp>);
        } else {
          if (c.controlTypeID === 'ImageCarousel') {
            delete c.links;
            delete c.interactiveData;
          }
          return (

            <Component key={'_' + c.controlID} {...c}>
              <Comp {...c}>
                {
                  c.items.map(function (_comp, index) {
                    //图片轮播组件把组件本身的填充模式传递到内层图片组件上
                    //这里之所以要把内层图片解析成组件，是考虑到以后图片轮播中可以添加其它组件，可以设置交互等
                    if (c.controlTypeID === 'ImageCarousel') {
                      _comp.fitMode = c.fitMode;
                      _comp.color = c.color;
                      _comp.w = c.w;
                      _comp.h = c.h;
                      _comp.currentW = c.currentW;
                      _comp.currentH = c.currentH;
                    }

                    //滚动区中内容层不再创建为一个组件
                    if (_comp.controlTypeID === 'ScrollViewContentPanel' || _comp.controlTypeID == 'ScrollViewPort') {
                      if (_comp.items) {
                        if (c.controlTypeID === 'ScrollArea') {
                          c.viewPortSize = adjustContentPanelChildPosition(_comp.items, c.contentLayout);
                        }
                        return _comp.items.map(function (itm) {

                          return makeComp(itm, comEvents);
                        });
                      }
                    }

                    //对所有组件中如果包含的有边框层的话，都进行忽略
                    if (_comp.controlTypeID !== 'Border') {
                      return makeComp(_comp, comEvents)
                    }
                  })
                }
              </Comp>
            </Component>
          );
        }
      }
      //普通组件，不包含子组件
      return (
        <Component key={'_' + c.controlID} {...c}>
          <Comp {...c}/>
        </Component>
      );
    }
    //拦截显示红字母版丢失
    if(c.controlTypeID === 'Master') {
      return (
        <div></div>
      );
    }
    console.warn('Cannot find component for ' + c.controlTypeID);
    return (
      <div style={{
        color: 'red'
      }}>Component {c.controlTypeID} is missing.</div>
    );
  }
  catch (e) {
    logHelper.error(e);
    return (
      <div style={{
        color: 'red'
      }}>Error in render {comp.controlTypeID}.</div>
    )
  }
}

function adjustContentPanelChildPosition(items, layout) {
  var x;
  var y;
  var w = 0;
  var h = 0;
  var i;
  var item;
  var l;
  if (layout === 'clRelatively') {
    if (items && items.length) {
      x = items[0].x;
      y = items[0].y;
      i = 1;
      l = items.length;
      if (l > i) {
        for (i; i < l; i++) {
          item = items[i];
          x = Math.min(x, item.x);
          y = Math.min(y, item.y);
        }
      }
      i = 0;
      x -= 1;//补偿左上角1像素的边界
      y -= 1;
      for (i; i < l; i++) {
        item = items[i];
        item.x -= x;
        item.y -= y;
        w = Math.max(w, item.x + item.w);
        h = Math.max(h, item.y + item.h);
      }
    }
  } else {
    l = items ? items.length : 0;
    for (i = 0; i < l; i++) {
      item = items[i];
      w = Math.max(w, item.x + item.w);
      h = Math.max(h, item.y + item.h);
    }
  }
  return {
    w: w + 2,//补偿两个像素的边界
    h: h + 2
  }
}

exports.makeComp = makeComp;



