'use strict';
var React = require('react');

var Tree = require('./Tree');

var MobilePreviewPanel = React.createClass({
  propTypes: {
    app: React.PropTypes.object,
    current: React.PropTypes.object,
    showPanel: React.PropTypes.bool.isRequired,
    onOpenPage: React.PropTypes.func.isRequired
  },
  render: function () {
    var app = this.props.app;
    var style = {};
    if (this.props.showPanel) {
      style.marginLeft = 0;
    } else {
      style.marginLeft = -200;
    }
    var modules = this.filterHiddenPage();
    return (
      <div className="mobile-preview-panel preview-panel" style={style}>
        <div className="header">
          <h1>{app.name}</h1>
        </div>
        <div className="pages">
          <Tree
            name={this.props.app.name}
            current={this.props.current}
            modules={modules}
            onSelectPage={this.props.onOpenPage}/>
        </div>
      </div>
    );
  },

  filterHiddenPage: function() {
    var isShowTitleIndex = this.props.app.params.projectParam.isShowTitleIndex;
    var isShowAllPageNode = this.props.isShowAllPageNode;     //是否显示所有隐藏页面
    var modules = JSON.parse(JSON.stringify(this.props.app.modules));

    var filter = function(pages, preIndex) {
      var count = 0;
      var page;
      var index;

      for(var i=pages.length-1;i>=0;i--) {
        var childs = 0;
        page = pages[i];
        if(page.isshownode === undefined) {     //兼容旧版本
          return this.props.app.modules;
        }
        if(!page.isshownode && !isShowAllPageNode) {
          pages.splice(i, 1);
        } else {
          if(isShowTitleIndex) {
            index = preIndex ? preIndex+'.'+(i+1) : (i+1)+'';
            page.name = index + ' ' + page.name;
          }
          if(page._pages && page._pages.length > 0) {
            childs = filter(page._pages, index);
            count += childs;
          }
          if(page.isModule) {
            page.childCount = childs;
          } else {
            count++;
          }
        }
      }
      return count;
    };

    if(isShowTitleIndex !== undefined) {    //兼容旧版本
      modules.childCount = filter(modules._pages);
    }
    return modules;
  }
});

module.exports = MobilePreviewPanel;


