'use strict';

var React = require('react');
var _ = require('underscore');
var i18n = require('../helper/i18n');

var IOSWarning = React.createClass({
  propTypes: {
    logo: React.PropTypes.string.isRequired,
    name: React.PropTypes.string.isRequired,
    arrowClassName: React.PropTypes.string.isRequired
  },
  render: function () {
    var logo,
      name,
      arrowClassName;
    logo = this.props.logo;
    name = this.props.name;
    arrowClassName = 'arrow ' + this.props.arrowClassName;
    return (
      <div className="ios-warning">
        <div className="content">
          <div className="header">
            <div className="logo">
              <img src={logo} alt={name} width="64" height="64"/>
            </div>
            <h2>{name}</h2>
          </div>
          <div className="how-to-install">
            <h3>{i18n.get('INSTALL')}</h3>
            {this.props.children}
            <p className="open-now">{i18n.get('SKIP_INSTALL')} <br/><a href={window.location.href + '?force=1'}>{i18n.get('OPEN_NOW')}</a></p>
          </div>
        </div>
        <div className={arrowClassName}>
          <img className="top-right-arrow"
               src="data:image/png;base64,"
               width="64"
               height="64"
               alt="click here"/>
          <img
            className="bottom-center-arrow"
            src="data:image/png;base64,"
            width="64"
            height="64"
            alt="click here"/>
        </div>
      </div>
    );
  }
});

module.exports = IOSWarning;


