/**
 * 配置数组节点, 出现这些节点时, 即使没有同名兄弟节点,也会放到数组中
 * @type {string[]}
 */
var arrayTags = ['page', 'module', 'state', 'link', 'control', 'event', 'behaviour', 'target', 'command', 'param', 'entry', 'row', 'col', 'cell'];

/**
 * 判断是否是数组
 * @param obj
 * @returns {boolean}
 */
function isArray(obj) {
  return toString.call(obj) === '[object Array]';
}

/**
 * 将文本转换为 DOM
 * @param str
 * @returns {*}
 */
function parseTODOM(str) {
  var xmlDoc;
  var parser;
  if (window.DOMParser) {
    parser = new DOMParser();
    xmlDoc = parser.parseFromString(str, 'text/xml');
  } else if (window.ActiveXObject) {
    xmlDoc = new ActiveXObject("Microsoft.XMLDOM");
    xmlDoc.async = false;
    xmlDoc.loadXML(str);
  } else {
    throw new Error('cannot parse xml');
  }
  return xmlDoc;
}

/**
 * 判断是否是文本节点
 * 规则:
 * 1. 仅包含一个子节点
 * 2. 子节点的节点名为 `#text`
 * @param node
 * @returns {boolean}
 */
function isTextNode(node) {
  return node.childNodes.length === 1 && node.childNodes[0].nodeName === '#text';
}

/**
 * 获取文本节点的值
 * @param obj
 * @returns {string}
 */
function getTextNode(obj) {
  return obj.childNodes[0].nodeValue;
}

function parseNode(xmlNode, result) {
  var length;
  var i;
  var jsonNode;
  var existing;
  var attribute;

  if (isTextNode(xmlNode)) {
    result[xmlNode.nodeName] = dataHelper.convertValue(xmlNode.nodeName, getTextNode(xmlNode));
    return;
  }

  if (xmlNode.nodeName === '#text' && xmlNode.nodeValue.trim() === '') { // ignore empty text node
    return;
  }
  jsonNode = {};
  if (xmlNode.nodeName === 'page' || xmlNode.nodeName === 'module') {
    jsonNode.isModule = xmlNode.nodeName === 'module';
    if (!result._pages) {
      result._pages = [];
    }
    result._pages.push(jsonNode);
  } else {
    existing = result[xmlNode.nodeName];
    if (existing) {
      if (!isArray(existing)) {
        result[xmlNode.nodeName] = [existing, jsonNode];
      } else {
        result[xmlNode.nodeName].push(jsonNode);
      }
    } else {
      if (arrayTags && arrayTags.indexOf(xmlNode.nodeName) != -1) {
        result[xmlNode.nodeName] = [jsonNode];
      } else {
        result[xmlNode.nodeName] = jsonNode;
      }
    }
  }

  if (xmlNode.attributes) {
    length = xmlNode.attributes.length;
    for (i = 0; i < length; i++) {
      attribute = xmlNode.attributes[i];
      if (xmlNode.nodeName === 'page' && attribute.nodeName === 'isdeleted' && attribute.nodeValue === 'True') {
        result._pages.pop(); // 移除最后添加的那个页面
        return
      }
      jsonNode[attribute.nodeName] = dataHelper.convertValue(attribute.nodeName, attribute.nodeValue);
    }
  }

  if (jsonNode.isfolder) {
    jsonNode.isModule = true;
  }

  length = xmlNode.childNodes.length;
  for (i = 0; i < length; i++) {
    parseNode(xmlNode.childNodes[i], jsonNode);
  }
}

function parseXMLToObject(str) {
  var dom = parseTODOM(str);
  var result = {};
  if (dom.childNodes.length) {
    parseNode(dom.childNodes[0], result);
  }
  return result;
}

/**
 * 递归替换组件controlID
 * @param comArray
 * @param idMap
 */
function doResetNewComID(comArray, idMap) {
  var i;
  var newComID;
  var com;
  var l = 0;
  if (comArray) {
    l = comArray.length;
  }
  for (i = 0; i < l; i++) {
    com = comArray[i];
    if (com.items !== undefined) {
      doResetNewComID(com.items, idMap);
    }

    // 重置组件ID号
    newComID = toolHelper.newGuid();

    idMap[comArray[i].controlID] = newComID;
    comArray[i].controlID = newComID;
  }
}

/**
 * 递归替换组件交互中targetID号
 * @param comArray
 * @param idMap
 */
function doResetNewTargetID(comArray, idMap) {
  var i;
  var com;
  var interactiveData;
  var entry;
  var entrys;
  var iEntry;
  var events;
  var iEvent;
  var event;
  var iBehaviour;
  var behaviour;
  var iTarget;
  var target;
  var oldTargetID;
  var newTargetID;
  if (comArray) {
    for (i = 0; i < comArray.length; i++) {
      com = comArray[i];
      if (com.items) {
        doResetNewTargetID(com.items, idMap);
      }

      interactiveData = com.interactiveData;
      if (!interactiveData)
        continue;

      entrys = interactiveData.entry;
      if (entrys) {
        for (iEntry = 0; iEntry < entrys.length; iEntry++) {
          entry = entrys[iEntry];
          if (entry.events && entry.events.event) {

            for (iEvent = 0; iEvent < entry.events.event.length; iEvent++) {
              event = entry.events.event[iEvent];

              for (iBehaviour = 0; iBehaviour < event.behaviour.length; iBehaviour++) {
                behaviour = event.behaviour[iBehaviour];

                for (iTarget = 0; iTarget < behaviour.target.length; iTarget++) {
                  target = behaviour.target[iTarget];

                  oldTargetID = target.targetID;
                  newTargetID = idMap[oldTargetID];
                  if (newTargetID) {
                    target.targetID = newTargetID;
                  }
                } // for (iTarget = 0)
              } // for(iBehaviour = 0)
            } // for (iEvent = 0;)
          }
        } // for (iEntry = 0)

      }

    }
  }
}

/**
 * 替换组件数组中的所有组件ID号，包括交互数据中targetID
 * @param comArray
 */
function resetNewComID(comArray) {
  var idMap = {};
  if (comArray) {
    doResetNewComID(comArray, idMap);

    // 替换组件内交互组件ID号。
    doResetNewTargetID(comArray, idMap);
  }
}

/**
 * 获取母版数据
 * @param masterID
 * @param cb
 * @returns {*}
 */
function getMaster(masterID, cb) {
  var master;
  // if (masterCache[masterID]) { // 优先使用 cache
  //   return cb(null, masterCache[masterID]);
  // }
  if (runtimeHelper.isLoadFromZip()) {
    try {
      master = parseXMLToObject(zip.files['Master/' + masterID + '.mbx'].asText().trim());
      masterCache[masterID] = master;
      cb(null, master);
    } catch (e) {
      cb(e);
    }
  } else if (runtimeHelper.isLoadFromEnterprise()) {
    apiHelper.loadResource('Master/' + masterID + runtimeHelper.getMBXExtension() + '?v=' + version, function (err, text) {
      if (err) {
        return cb(err);
      }
      master = parseXMLToObject(text);
      masterCache[masterID] = master;
      cb(null, master);
    });
  } else if (runtimeHelper.isLoadFromRemote() || runtimeHelper.isLoadFromMockplus()) {
    apiHelper.loadResource('app/Master/' + masterID + runtimeHelper.getMBXExtension() + '?v=' + version, function (err, text) {
      if (err) {
        return cb(err);
      }
      master = parseXMLToObject(text);
      masterCache[masterID] = master;
      cb(null, master);
    });
  } else {
    var node = document.getElementById(masterID);
    if (!node) {
      return cb(new Error('cannot find content of master:' + masterID));
    }
    var masterContent = node.innerHTML.trim();
    try {
      master = parseXMLToObject(masterContent);
      masterCache[masterID] = master;
      cb(null, master);
    } catch (e) {
      cb(e);
    }
  }
}

/**
 * 替换页面中所有母版组件
 * @param comArray 组件列表
 * @param cb 回调函数
 */
function doReplacePageMasterCom(comArray, cb) {
  var totalCount = 0;
  var needReplace;
  var callback;
  var updateChildVisible;

  if (comArray.length === 0) {
    return cb();
  }

  needReplace = _.filter(comArray, function (com) {
    return com.controlTypeID === 'Master' && com.items;
  });

  if (needReplace.length === 0) {
    return cb();
  }

  callback = _.after(totalCount, function () {
    cb();
  });

  updateChildVisible = function (items, isShow) {
    var com;
    var i;
    var l = 0;
    if (items) {
      l = items.length;
      for (i = 0; i < l; i++) {
        com = items[i];
        com.isShow = isShow;
        if (com.items) {
          updateChildVisible(com.items, isShow);
        }
      }
    }
  };

  _.each(needReplace, function (com) {
    if (com.controlTypeID === 'Master') {
      com.controlTypeID = '__group__';
      getMaster(com.masterID, function (err, masterData) {
        if (!err) {
          Reconstruction.reconstruction(masterData);

          com.items = masterData.master.controls.control[0].items;
          if(versionHelper.isPageVersionLowerThan4_9()) {
            updateChildVisible(com.items, com.isShow);
          }
          resetNewComID(com.items);
        }
        callback();
      });
    } else {
      doReplacePageMasterCom(com.items, function () {
        callback();
      });
    }
  });
}

