function doReplaceContentPanelComData(comRootArray, comSourceArray, pageIDStack, cb) {
  var i;
  var com;
  var links;
  var targetPageID;
  var link;

  if (!comSourceArray || comSourceArray.length === 0) {
    return cb();
  }

  var afterAll = _.after(comSourceArray.length, function () {
    cb();
  });

  function loadPageDone(err, pageData) {
    if (err) {
      return cb(err);
    }
    if (!pageData) {
      afterAll();
      return;
    }
    Reconstruction.reconstruction(pageData);
    if (!pageData.controls.control) { // 没有组件
      return afterAll();
    }
    // 首先替换页面数中的所有组件ID号，防止与其他页面中组件ID号冲突
    resetNewComID(pageData.controls.control);
    com.items = [];
    doMergeContentPanelComData(comRootArray, com.items, pageData.controls.control, pageIDStack, function (err) {
      if (err) {
        return cb(err);
      }
      //
      // 该页面加载完成，则从栈顶退出，能够保证不会出现循环加载情况，不退出则会造成不能第二次加载相同页面
      //
      //  如：
      //  A => B => C
      //  A => C
      //
      //  A页面包含B和C两个页面，B页面中包含C页面
      //
      //
      loadingPageStack.pop();
      afterAll();
    });
  }

  for (i = 0; i < comSourceArray.length; i++) {
    com = comSourceArray[i];

    links = com.links;
    /*if (!links || !links.link) {
      afterAll();
      continue;
    }*/

    link = links ? links.link[0] : {};
    if (links && (link.linkType === appTypes.PageLinkType.INCLUDE_PAGE) && (link.isApplyIncludePage)) {
      targetPageID = link.targetPageID;
      if (!targetPageID || loadingPageStack.indexOf(targetPageID) !== -1) {
        afterAll();
        continue;
      }

      // 把当前页面加入到栈队列中
      loadingPageStack.push(targetPageID);

      loadPage(targetPageID, loadPageDone);

    } else {
      // 遍历子合并子节点
      if (com.items) {
        doReplaceContentPanelComData(comRootArray, com.items, pageIDStack, afterAll);
      } else {
        afterAll();
      }
    }
  }

}

/**
 * 合并组建内容数据资源
 * @param comRootArray 带插入的root节点Array
 * @param comInsertArray 当前带插入的节点的Array
 * @param comSourceArray 组件列表数据
 * @param pageIDStack 页面ID内容栈
 */
function doMergeContentPanelComData(comRootArray, comInsertArray, comSourceArray, pageIDStack, cb) {

  var comList = [];
  var com;
  var i;

  for (i = 0; i < comSourceArray.length; i++) {
    com = comSourceArray[i];
    comList.push(com);

    if (pageHelper.IsInvisualInteractiveCom(com.controlTypeID)) {
      comRootArray.push(com);
    } else {
      comInsertArray.push(com);
    }
  }

  doReplaceContentPanelComData(comRootArray, comList, pageIDStack, function (err) {
    if (err) {
      return console.error(err);
    }
    cb();
  });
}

var loadingPageStack = [];

/**
 * 合并生成预览数据，替换组件ID号，合并内容面板数据。
 *
 * @param comArray 根节点control对象
 */
function mergeContentPanelComData(comArray, cb) {
  var pageIDStack = [];
  var comRootArray = [];

  // 第一层节点中，rootArray和InsertArray为同一个。
  doMergeContentPanelComData(comRootArray, comRootArray, comArray, pageIDStack, function (err) {
    if (err) {
      return cb(err);
    }
    cb(null, comRootArray);
  });
}

// ---------------------------------------------------------------------------------------------------------------------

/**
 * 替换页面中的所有母版资源
 * @param pageData
 */
function getRealPageComData(pageData, cb) {
  var controlsNode = pageData.mockup.controls;
  if (!controlsNode.control) {
    return cb();
  }
  // 重新生成组件数据对象
  mergeContentPanelComData(controlsNode.control, function (err, comRootArray) {
    if (err) {
      return cb(err);
    }
    controlsNode.control = comRootArray;
    // 替换页面中母版组件数据
    doReplacePageMasterCom(controlsNode.control, function (err) {
      if (err) {
        return cb(err);
      }
      // 重新生成所有组件中组件ID号
      resetNewComID(controlsNode.control);
      cb();
    });
  });
}

function parseXMLToPage(text, id, cb) {
  var page;
  var obj = parseXMLToObject(text);
  Reconstruction.reconstruction(obj);

  return getRealPageComData(obj, function (err) {
    if (err) {
      return cb(err);
    }

    page = obj.mockup;
    page.ID = id; // 有时候页面内的 ID 不是页面的真实 ID

    page.title = getPageTitleByPageID(currentProject.modules, id);
    page = pageHelper.afterLoadPrepareDataStructure(page);
    pagesCache[id] = JSON.parse(JSON.stringify(page)); // 保存副本
    cb(null, page);
  });
}

/**
 * 加载页面数据
 * @param id
 * @param cb
 * @returns {*}
 */
function loadPage(id, cb) {
  if (id === 'command:back') {
    return cb(null);
  }
  var node;
  var xml;
  if (pagesCache[id]) {
    return cb(null, JSON.parse(JSON.stringify(pagesCache[id]))); // 克隆一份使用
  }

  if (runtimeHelper.isLoadFromRemote() || runtimeHelper.isLoadFromMockplus()) {
    apiHelper.loadResource('app/MBX/' + id + runtimeHelper.getMBXExtension() + '?v=' + version, function (err, text) {
      if (err) {
        return cb(err);
      }
      parseXMLToPage(text, id, cb);
    });
  } else if (runtimeHelper.isLoadFromEnterprise()) {
    apiHelper.loadResource('MBX/' + id + runtimeHelper.getMBXExtension() + '?v=' + version, function (err, text) {
      if (err) {
        return cb(err);
      }
      parseXMLToPage(text, id, cb);
    });
  } else if (runtimeHelper.isLoadFromZip()) {
    setTimeout(function () { // 用于模拟线上异步加载的, 不要去掉
      try {
        xml = zip.files['MBX/' + id + '.mbx'].asText().trim();
        parseXMLToPage(xml, id, cb);
      } catch (e) {
        console.error('fail to load page', id);
        cb(e);
      }
    }, 100);
  } else {
    node = document.getElementById(id);
    if (!node) {
      return cb(new Error('cannot find content of page:' + id));
    }
    xml = node.innerHTML.trim();
    parseXMLToPage(xml, id, cb);
  }
}

/**
 * 载入单个页面文件
 * @param id
 * @param cb
 */
exports.loadPage = loadPage;

/**
 * 载入图片
 * @param name
 * @returns {*}
 */
exports.loadImage = function (name) {
  var file;
  if (imageCache[name]) {
    return imageCache[name];
  }
  if (runtimeHelper.isLoadFromZip()) {
    try {
      file = zip.files['0EDCD3EB-42DB-412B-9411-E5DD751EB4BA/' + name];
      if (file) {
        imageCache[name] = URL.createObjectURL(new Blob([file.asArrayBuffer()], {type: 'image/png'}));
        return imageCache[name];
      }
      console.warn('image', name, 'not exists');
    } catch (e) {
      console.error(e);
    }
    return null;
  }
  if (runtimeHelper.isLoadFromEnterprise()) {
    imageCache[name] = '0EDCD3EB-42DB-412B-9411-E5DD751EB4BA/' + name;
    return '0EDCD3EB-42DB-412B-9411-E5DD751EB4BA/' + name;
  }
  imageCache[name] = currentPath + '/0EDCD3EB-42DB-412B-9411-E5DD751EB4BA/' + name;
  return currentPath + '/0EDCD3EB-42DB-412B-9411-E5DD751EB4BA/' + name;
};

exports.loadSVG = function (name) {
  var file;
  if (!name) {
    return;
  }
  if (imageCache[name]) {
    return imageCache[name];
  }
  if (runtimeHelper.isLoadFromZip()) {
    try {
      file = zip.files['0EDCD3EB-42DB-412B-9411-E5DD751EB4BA/' + name];
      if (file) {
        imageCache[name] = URL.createObjectURL(new Blob([file.asArrayBuffer()], {type: 'image/svg+xml'}));
        return imageCache[name];
      }
      console.warn('svg', name, 'not exists');
    } catch (e) {
      console.error(e);
    }
    return null;
  }
  if (runtimeHelper.isLoadFromEnterprise()) {
    imageCache[name] = '0EDCD3EB-42DB-412B-9411-E5DD751EB4BA/' + name;
    return '0EDCD3EB-42DB-412B-9411-E5DD751EB4BA/' + name;
  }
  imageCache[name] = currentPath + '/0EDCD3EB-42DB-412B-9411-E5DD751EB4BA/' + name;
  return currentPath + '/0EDCD3EB-42DB-412B-9411-E5DD751EB4BA/' + name;
};

/**
 * 根据页面ID号获取页面标题
 * @param modules 所有页面Module数据信息
 * @param ID 页面ID号
 * @returns 返回页面标题
 */
function getPageTitleByPageID(modules, ID) {
  var i;
  var node;

  for (i = 0; i < modules._pages.length; i++) {
    node = modules._pages[i];
    if (node.ID === ID) {
      return node.name;
    }

    if (node._pages && node._pages.length) {
      var pageTitle = getPageTitleByPageID(node, ID);
      if (pageTitle !== undefined) {
        return pageTitle;
      }
    }
  }
}

if (!String.prototype.startsWith) {
  String.prototype.startsWith = function (searchString, position) {
    position = position || 0;
    return this.substr(position, searchString.length) === searchString;
  };
}

function getGoToPage() {
  var search = window.location.search;
  var searches = search.split(/[?@]/g);
  for (var i = 0; i < searches.length; i++) {
    var item = searches[i];
    if (item.startsWith('to=')) {
      return item.substring(3);
    }
  }
  return '';
}

function doGetAppFirstPage(module) {
  var i;
  var _page;
  var l;
  var rsPage;

  if (!module._pages) {
    return;
  }
  for (i = 0, l = module._pages.length; i < l; i++) {
    _page = module._pages[i];
    if (!_page.isModule) {
      return _page;
    } else {
      rsPage = doGetAppFirstPage(_page);
      if (rsPage) {
        return rsPage;
      }
    }
  }
}

/**
 * 获取项目第一个页面数据对象
 * @param modules
 */
function getAppFirstPage(modules) {
  return doGetAppFirstPage(modules);
}

/**
 * 获取第一个加载的页面ID
 * @returns {*}
 */
exports.getAppFirstPage = function () {
  var goToPage = urlHelper.getQueryString('to');
  if (goToPage) {
    return goToPage;
  }
  firstPageCache = firstPageCache || getAppFirstPage(currentProject.modules);
  return firstPageCache && firstPageCache.ID;
};

/**
 * 将 XML 文本转换为对象
 * @type {parseXMLToObject}
 */
exports.parseXMLToObject = parseXMLToObject;

function parseXMLToApp(text, cb) {
  var obj = parseXMLToObject(text);
  if (obj && obj.html && obj.html.body && obj.html.body.parsererror && obj.html.body.parsererror.div) {
    return cb(new Error(obj.html.body.parsererror.div));
  }
  if (obj.project.parsererror) {
    return cb(new Error(obj.project.parsererror.div));
  }
  if (obj.project.pages) {
    obj.project.modules = obj.project.pages;
  }
  normalizeAppDataAndCache(obj);
  cb(null, obj.project);
}

/**
 * 载入项目文件
 * @param path 项目文件路径
 * @param cb
 */
exports.loadApp = function (path, cb) {
  var html;
  // 加载项目时清理 cache
  masterCache = {};
  pagesCache = {};
  firstPageCache = null;

  if (runtimeHelper.isLoadFromZip()) {
    JSZipUtils.getBinaryContent(path + '?v=' + Math.random(), function (err, data) {
      if (err) {
        cb(err);
        return;
      }
      zip = new JSZip(data);
      html = zip.files['Main.mpk'].asText().trim();
      parseXMLToApp(html, cb);
    });
  }
  else if (runtimeHelper.isLoadFromEnterprise()) {
    apiHelper.loadResource('Main' + runtimeHelper.getMPKExtension() + '?v=' + version, function (err, text, statusCode) {
      if (err) {
        console.error(err);
        cb(err, null, statusCode);
        return;
      }
      parseXMLToApp(text, cb);
    });
  } else if (runtimeHelper.isLoadFromRemote() || runtimeHelper.isLoadFromMockplus()) {
    apiHelper.loadResource('app/Main' + runtimeHelper.getMPKExtension() + '?v=' + version, function (err, text, statusCode) {
      if (err) {
        console.error(err);
        cb(err, null, statusCode);
        return;
      }
      parseXMLToApp(text, cb);
    });
  } else {
    var node = document.getElementById('main');
    if (!node) {
      return cb(new Error('cannot find content of app'));
    }
    html = node.innerHTML.trim();
    parseXMLToApp(html, cb);
  }
};

/**
 * 处理 app 数据
 */
function normalizeAppDataAndCache(obj) {
  currentProject = obj.project; // cache current project;
  countChildPages(obj.project.modules);
  obj.project.modules.isModule = true;
  obj.project.modules.childCount = countANode(obj.project.modules);
}

function countANode(node) {
  if (node.childCount) {
    return node.childCount;
  }
  if (node._pages) {
    var childCount = _.reduce(node._pages, function (memo, item) {
      return memo + countANode(item);
    }, 0);
    return node.isModule ? childCount : childCount + 1;
  }
  return node.isModule ? 0 : 1;
}

function countChildPages(node) {
  _.each(node._pages, function (item) {
    item.childCount = countANode(item);
    countChildPages(item);
  });
}

exports.auth = function (pwd, cb) {
  request.post('/auth')
    .send("id=" + getAppID())
    .send("password=" + pwd)
    .end(function (err, res) {
      cb(err, res);
    });
};


function getAppID() {
  var re = /\/([^\/]+)\/index.html/i;
  var match = re.exec(window.location.href);
  if (match) {
    return match[1];
  }
  return '';
}



