function changeFileName(name) {

  name = name.replace(/,|\s|\(|\)/g, '_');

  //去掉重复的下划线
  name = name.replace(/(_)_*\1/g, "_");

  if(name.indexOf('.png') === -1 && name.indexOf('.jpg') === -1) {
    name = name + ".png";
  }

  return name.toLowerCase();
}

/**
 * 根据资源的相对路径查找真实路径
 * @param p
 * @returns {string}
 */
exports.getAsset = function (p) {
  return path.join(prefix + 'assets/', p);
};

/**
 * 获取组件的素材
 * @param name
 * @returns {string}
 */
exports.getComponentAsset = function (name) {
  if (!name) {
    return '';
  }
  name = changeFileName(name);
  return prefix + 'assets/components/wireframe/' + name.toLowerCase(); // 这里始终使用线框素材
};

/**
 * 获取图标
 * @param filename
 * @returns {string}
 */
exports.getIcon = function (filename) {
  if (!filename) {
    return '';
  }
  filename = changeFileName(filename);
  return prefix + 'assets/icons/wireframe/' + filename.toLowerCase(); // 始终使用线框图标
};

/**
 * 获取辅助素材资源
 * @param filename
 * @returns {string}
 */
exports.getAssistAsset = function (filename) {
  if (!filename) {
    return '';
  }
  filename = changeFileName(filename);
  return prefix + 'assets/components/wireframe/commons/' + filename.toLowerCase();
};

exports.getLogo = function () {
  return prefix + 'assets/logo.png';
};



