'use strict';
var util = require('./util');

function setTableCellDefaultProps(cell) {
  if (cell.fontSize === undefined) {
    cell.fontSize = 12;
  }
  if (cell.backColor === undefined) {
    cell.backColor = '#FFFFFF';
  }
  if (cell.textHAlign === undefined) {
    cell.textHAlign = 'HaCenter';
  }
  if (cell.textVAlign === undefined) {
    cell.textVAlign = 'VaMiddle';
  }
  if (cell.textColor === undefined) {
    cell.textColor = "#000000";
  }
  if (cell.fontFamily === undefined) {
    cell.fontFamily = "";
  }
  if (cell.textStyles === undefined) {
    cell.textStyles = {};
  }
  if (cell.fontWeight === undefined) {
    cell.fontWeight = "Regular";
  }
  if (cell.fontSize === undefined) {
    cell.fontSize = 12;
  }
  if (cell.textWordWrap === undefined) {
    cell.textWordWrap = true;
  }
  if (cell.leftBorderVisible === undefined) {
    cell.leftBorderVisible = true;
  }
  if (cell.topBorderVisible === undefined) {
    cell.topBorderVisible = true;
  }
  if (cell.rightBorderVisible === undefined) {
    cell.rightBorderVisible = true;
  }
  if (cell.bottomBorderVisible === undefined) {
    cell.bottomBorderVisible = true;
  }
  if (cell.cellBorderVisible !== undefined) {
    cell.leftBorderVisible = cell.cellBorderVisible;
    cell.topBorderVisible = cell.cellBorderVisible;
    cell.rightBorderVisible = cell.cellBorderVisible;
    cell.bottomBorderVisible = cell.cellBorderVisible;
  }
  if (cell.leftBorderThinckness === undefined) {
    cell.leftBorderThinckness = 1;
  }
  if (cell.topBorderThinckness === undefined) {
    cell.topBorderThinckness = 1;
  }
  if (cell.rightBorderThinckness === undefined) {
    cell.rightBorderThinckness = 1;
  }
  if (cell.bottomBorderThinckness === undefined) {
    cell.bottomBorderThinckness = 1;
  }
  if (cell.cellBorderThinckness !== undefined) {
    cell.leftBorderThinckness = cell.cellBorderThinckness;
    cell.topBorderThinckness = cell.cellBorderThinckness;
    cell.rightBorderThinckness = cell.cellBorderThinckness;
    cell.bottomBorderThinckness = cell.cellBorderThinckness;
  }
  if (cell.leftBorderColor === undefined) {
    cell.leftBorderColor = "#C5C5C5";
  }
  if (cell.topBorderColor === undefined) {
    cell.topBorderColor = "#C5C5C5";
  }
  if (cell.rightBorderColor === undefined) {
    cell.rightBorderColor = "#C5C5C5";
  }
  if (cell.bottomBorderColor === undefined) {
    cell.bottomBorderColor = "#C5C5C5";
  }
  if (cell.cellBorderColor !== undefined) {
    cell.leftBorderColor = cell.cellBorderColor;
    cell.topBorderColor = cell.cellBorderColor;
    cell.rightBorderColor = cell.cellBorderColor;
    cell.bottomBorderColor = cell.cellBorderColor;
  }
  if (cell.leftBorderDashMode === undefined) {
    cell.leftBorderDashMode = "SdSolid";
  }
  if (cell.topBorderDashMode === undefined) {
    cell.topBorderDashMode = "SdSolid";
  }
  if (cell.rightBorderDashMode === undefined) {
    cell.rightBorderDashMode = "SdSolid";
  }
  if (cell.bottomBorderDashMode === undefined) {
    cell.bottomBorderDashMode = "SdSolid";
  }
  if (cell.cellBorderDashMode !== undefined) {
    cell.leftBorderDashMode = cell.cellBorderDashMode;
    cell.topBorderDashMode = cell.cellBorderDashMode;
    cell.rightBorderDashMode = cell.cellBorderDashMode;
    cell.bottomBorderDashMode = cell.cellBorderDashMode;
  }
}

function buildCoords(rows, cols) {
  var horiOff = 0;
  var vertOff = 0;
  var width, height;
  var coords = [];
  for (var i = 0; i < rows.length; i++) {
    height = Math.round(rows[i].height);
    coords.push([]);
    horiOff = 0;
    for (var j = 0; j < cols.length; j++) {
      width = Math.round(cols[j].width);
      var rect = util.Rect.create(horiOff, vertOff, width, height);
      coords[i].push(rect);
      horiOff += width;
    }
    vertOff += height;
  }
  return coords;
}

function buildMatrix(rows, cols, cells) {
  var matrix = [];
  for (var i = 0; i < rows.length; i++) {
    matrix.push([]);
    for (var j = 0; j < cols.length; j++) {
      matrix[i].push({});
    }
  }
  for (var i = 0; i < cells.length; i++) {
    var rowArr = cells[i].rows.split(',');
    var colArr = cells[i].cols.split(',');
    for (var j = 0; j < rowArr.length; j++) {
      var rowIndex = rowArr[j];
      for (var k = 0; k < colArr.length; k++) {
        var colIndex = colArr[k];
        matrix[rowIndex][colIndex] = cells[i];
        setTableCellDefaultProps(cells[i]);
      }
    }
    delete cells[i].rows;
    delete cells[i].cols;
  }
  return matrix;
}

module.exports = {
  setTableCellDefaultProps: setTableCellDefaultProps,
  buildCoords: buildCoords,
  buildMatrix: buildMatrix
};



